export default {
  name: 'Vacation',
  components: {
  },
  data () {
    return {
      vacationRecord: [],
      isHelpOpen: false,
      message: ''
    }
  },
  mounted () {

  },
  methods: {
    addVacation () {
      this.$router.push('/vacationInfo')
    },
    vacationDetail () {
      this.$router.push('/vacationDetail')
    },
    openHelp () {
      this.isHelpOpen = true
    },
    closeHelp () {
      this.isHelpOpen = false
    }
  }
}



