
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import echarts from 'echarts'

export default {
name: 'PayRollReport',
components: {
CellFormPreview,
Group,
Cell,
Datetime,
XTable,
Grid,
GridItem
},
data () {
return {
showLoading: true,
vocationManageList: [],
value1: '搜索日期',
payRollReportList: '',
payRollReportDateList: '',
options: [
{text: '上月', value: ''},
{text: '最近三月', value: '2'},
{text: '最近半年', value: ''},
{text: '最近一年', value: ''},
{text: '年初至今', value: '0'}
],
ceshi: {
a: '1'
},
selected: '0'
}
},
mounted () {

console.log(this.ceshi)
this.choosePayRoll(0)
},

methods: {
goTarget (item, index) {
this.$router.push({
name: 'payRollData',
query: {
hrPayrollReportRealSalaryDate: item.hrPayrollReportRealSalaryDate
}
})
},
showPie (itemReport) {
let option = {
tooltip: { // 提示框组件
trigger: 'item', // 触发类型(饼状图片就是用这个)
formatter: '{a} <br/>{b} : {c} ({d}%)', // 提示框浮层内容格式器
position: [3, 3]
},
color: ['#48cda6', '#fd87ab', '#ffdf33', '#968ade'], // 手动设置每个图例的颜色
legend: { // 图例组件


width: 200, // 图行例组件的宽度,默认自适应


x: '5%', // 图例显示在右边
y: '78%',
bottom: 10,
itemWidth: 10, // 图例标记的图形宽度
itemHeight: 10, // 图例标记的图形高度
data: ['个人所得税', '四金个人缴纳', '实发工资'],
textStyle: { // 图例文字的样式
color: 'white', // 文字颜色
fontSize: 9 
}
},
series: [ // 系列列表
{
name: '薪水信息', // 系列名称
type: 'pie', // 类型 pie表示饼图
center: ['30%', '40%'], // 设置饼的原心坐标 不设置就会默认在中心的位置
radius: ['50%', '70%'], // 饼图的半径,第一项是内半径,第二项是外半径,内半径为0就是真的饼,不是环形
itemStyle: { // 图形样式
normal: { // normal 是图形在默认状态下的样式；emphasis 是图形在高亮状态下的样式，比如在鼠标悬浮或者图例联动高亮时。
label: { // 饼图图形上的文本标签
show: false 
},
labelLine: { // 标签的视觉引导线样式
show: false 
}
},
emphasis: { // normal 是图形在默认状态下的样式；emphasis 是图形在高亮状态下的样式，比如在鼠标悬浮或者图例联动高亮时。
label: { // 饼图图形上的文本标签
show: false,
position: 'center',
textStyle: {
fontSize: '10',
fontWeight: 'bold'
}
}
}
},
data: [
{value: 100, name: '个人所得税'},
{value: 200, name: '四金个人缴纳'},
{value: 300, name: '实发工资'}
]
}
]
}
let myChart = echarts.init(document.getElementById('main2'))
myChart.setOption(option)
},

choosePayRoll (rowReport) {
this.showPie()
this.payRollReportList = [ {'hrPayrollReportBasePercent': 11} ]
}
}
}



